<script>
  import MarginSelect from './margin-select'
  import DisplaySelect from './display-select'
  import FontSelect from './font-select'
  import BoxShadowSelect from './box-shadow-select'
  import BorderSelect from './border-select'
  import Upload from '@/components/Upload'
  import StyleInput from '@/components/styleInput'
  import { getNodeByElementId, getParentNodeByElementId, isFlexElement } from '@/utils/tree-utils'
  import _ from 'lodash'
  import ImageSelect from '@/components/SourceManage/imageSelect.vue'
  import { getPropsValues } from '@/renderer/tools/config'
  import { pageInject } from '@/renderer/tools/pageStore'
  import AnimateEditDialog from './animate-edit-dialog.vue'
import { uploadPrefix } from '@/config/code-var'

  export default {
    name: 'StyleForm',
    components: {
      CssCode: () => import('@/components/CssCode'),
      MarginSelect,
      DisplaySelect,
      FontSelect,
      BoxShadowSelect,
      BorderSelect,
      Upload,
      StyleInput,
      ImageSelect,
      AnimateEditDialog,
    },
    inject: ['dndOptions', ...pageInject],
    props: {
      config: {
        type: Object,
      },
      animation: {
        type: Array,
      },
      styles: {
        type: Object,
      },
      isRoot: {
        type: Boolean,
      },
    },
    data() {
      return {
        activeCollapse: ['flex', 'size', 'display', 'margin', 'font', 'background', 'border', 'others', 'animate'],
        styleFormData: {},
        cursorList: ['default', 'auto', 'pointer', 'move', 'not-allowed', 'copy' ,'text', 'none'],
        isShowConfig: true,
        uploadAction: `${uploadPrefix}/${this.$route.query.appId}/images`,
        editAnimateIndex: undefined,
      }
    },
    computed: {
      elementId() {
        return this.dndOptions.selectedId
      },
      currentElement() {
        return getNodeByElementId(this.getStore().layoutData, this.elementId)
      },
      isShowFlex() {
        const parentNode = getParentNodeByElementId(this.getStore().layoutData, this.elementId)
        if (parentNode) {
          const parentElement = document.querySelector(`.page-canvas [data-zone="${parentNode.elementId}_${parentNode.elementType}"]`)
          return this.isRoot && isFlexElement(parentElement)
        }
        return false
      },
      styleConfig: {
        get() {
          return this.styles
        },
        set(n) {
          this.styles = n
          this.$emit('update:styles', n)
          this.$emit('change')
        },
      },
      backgroundColor: {
        get() {
          return this.styleConfig.backgroundColor
        },
        set(n) {
          this.styleConfig = {
            ...this.styleConfig,
            backgroundColor: n,
          }
        },
      },
      backgroundImage: {
        get() {
          return this.styleConfig.backgroundImage
        },
        set(n) {
          this.styleConfig = {
            ...this.styleConfig,
            backgroundImage: n,
          }
        },
      },
      margin: {
        get() {
          return {
            top: this.styleConfig.marginTop,
            right: this.styleConfig.marginRight,
            bottom: this.styleConfig.marginBottom,
            left: this.styleConfig.marginLeft,
          }
        },
        set(n) {
          this.styleConfig = {
            ...this.styleConfig,
            marginTop: n.top,
            marginRight: n.right,
            marginBottom: n.bottom,
            marginLeft: n.left,
          }
        },
      },
      padding: {
        get() {
          return {
            top: this.styleConfig.paddingTop,
            right: this.styleConfig.paddingRight,
            bottom: this.styleConfig.paddingBottom,
            left: this.styleConfig.paddingLeft,
          }
        },
        set(n) {
          this.styleConfig = {
            ...this.styleConfig,
            paddingTop: n.top,
            paddingRight: n.right,
            paddingBottom: n.bottom,
            paddingLeft: n.left,
          }
        },
      },
      display: {
        get() {
          return {
            display: this.styleConfig.display,
            alignItems: this.styleConfig.alignItems || 'center',
            justifyContent: this.styleConfig.justifyContent || 'center',
            flexDirection: this.styleConfig.flexDirection || 'row',
            flexWrap: this.styleConfig.flexWrap || 'nowrap',
          }
        },
        set(n) {
          this.styleConfig = {
            ...this.styleConfig,
            ...n,
          }
        },
      },
      fontStyleData: {
        get() {
          return {
            fontFamily: this.styleConfig.fontFamily,
            fontSize: this.styleConfig.fontSize,
            lineHeight: this.styleConfig.lineHeight,
            fontWeight: this.styleConfig.fontWeight,
            textAlign: this.styleConfig.textAlign,
            color: this.styleConfig.color,
            whiteSpace: this.styleConfig.whiteSpace,
          }
        },
        set(n) {
          const result = {
            ...this.styleConfig,
            ...n,
          }
          // 当前有改变到whiteSpace的时候
          if(n.whiteSpace !== this.styleConfig.whiteSpace) {
            result.overflowX = n.whiteSpace === 'normal' ? 'hidden' : 'visible'
          }
          this.styleConfig = result
        },
      },
      border: {
        get() {
          const borderLeftArr = this.styleConfig.borderLeft?.split(' ') || []
          const borderRightArr = this.styleConfig.borderRight?.split(' ') || []
          const borderTopArr = this.styleConfig.borderTop?.split(' ') || []
          const borderBottomArr = this.styleConfig.borderBottom?.split(' ') || []
          const all = [borderLeftArr, borderRightArr, borderTopArr, borderBottomArr]
          const borderArr = all.find(item => item.length > 1) || []
          let placement = []
          all.forEach((item, index) => {
            if (item.length > 1) {
              switch (index){
                case 0:
                  placement.push('左')
                  break
                case 1:
                  placement.push('右')
                  break
                case 2:
                  placement.push('上')
                  break
                case 3:
                  placement.push('下')
                  break
              }
            }
          })
          if (!placement.length) {
            placement = ['上', '下', '左', '右']
          }
          return {
            placement,
            width: borderArr[0],
            type: borderArr[1],
            color: borderArr[2],
          }
        },
        set(n) {
          let borderConfig = {
            borderTop: 'none',
            borderLeft: 'none',
            borderBottom: 'none',
            borderRight: 'none'
          }
          let border = undefined
          switch (n) {
            case undefined:
              border = undefined
              borderConfig.borderTop = undefined
              borderConfig.borderLeft = undefined
              borderConfig.borderBottom = undefined
              borderConfig.borderRight = undefined
              break
            case 'none':
              border = 'none'
              borderConfig.borderTop = 'none'
              borderConfig.borderLeft = 'none'
              borderConfig.borderBottom = 'none'
              borderConfig.borderRight = 'none'
              break
            default:
              border = `${n.width} ${n.type} ${n.color}`
              n.placement.forEach(p => {
                switch (p) {
                  case '上':
                    borderConfig.borderTop = border
                    break
                  case '左':
                    borderConfig.borderLeft = border
                    break
                  case '下':
                    borderConfig.borderBottom = border
                    break
                  case '右':
                    borderConfig.borderRight = border
                    break
                }
              })
              break
          }
          this.styleConfig = {
            ...this.styleConfig,
            ...borderConfig,
          }
        },
      },
      boxShadow: {
        get() {
          const boxShadowArr = this.styleConfig.boxShadow?.split(' ') || []
          return {
            x: boxShadowArr[0],
            y: boxShadowArr[1],
            blur: boxShadowArr[2],
            spread: boxShadowArr[3],
            color: boxShadowArr[4],
          }
        },
        set(n) {
          let boxShadow = undefined
          switch (n) {
            case undefined:
              boxShadow = undefined
              break
            case 'none':
              boxShadow = 'none'
              break
            default:
              boxShadow = `${n.x} ${n.y} ${n.blur} ${n.spread} ${n.color}`
          }
          this.styleConfig = {
            ...this.styleConfig,
            boxShadow,
          }
        },
      },
      isRectDisabled() {
        const parentNode = getParentNodeByElementId(this.getStore().layoutData, this.elementId)
        return this.isRoot  && parentNode && ['grid-dnd'].includes(parentNode.elementType)
      },
      isDisplayDisabled() {
        const { elementType } = this.currentElement || {}
        return this.isRoot  && ['grid-dnd', 'layout-container', 'form-container'].includes(elementType)
      },
      isSourceDisabled() {
        const { elementType } = this.currentElement || {}
        return this.isRoot  && ['grid-dnd'].includes(elementType)
      },
      baseConfigList() {
        const currentElement = this.getElementPropsById(this.dndOptions.selectedId)
        const { styleConfigList: baseConfigList = [] } = getPropsValues({ elementType: currentElement.elementType, elementVersion: currentElement.elementVersion })
        return baseConfigList
      }
    },
    watch: {
      elementId: {
        immediate: true,
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.initProps()
        }
      },
    },
    methods: {
      initProps() {
        this.styleFormData = _.cloneDeep(this.styleConfig)
        this.isShowConfig = false
        this.$nextTick(() => {
          this.isShowConfig = true
        })
      },
      handleChange(value, key) {
        this.styleConfig = {
          ...this.styleConfig,
          [key]: value,
        }
      },
      handleCssTextChang(isFocus) {
        if (!isFocus) {
          this.handleChange(this.styleFormData.cssText, 'cssText')
        }
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      handleShowEdit() {
        this.$emit('edit', this.config)
      },
      handleEditAnimate(animate, index) {
        this.editAnimateIndex = index
        this.$refs.animateEditDialog.open(animate)
      },
      handleAddAnimate() {
        this.$refs.animateEditDialog.open()
      },
      handleRemoveAnimate(index) {
        this.animation.splice(index, 1)
        this.$emit('update:animation', this.animation)
        this.$emit('change')
      },
      handleAnimateAddConfirm(animate) {
        this.animation = [...this.animation, animate]
        this.$emit('update:animation', this.animation)
        this.$emit('change')
      },
      handleAnimateEditConfirm(animate) {
        this.animation[this.editAnimateIndex] = animate
        this.$emit('update:animation', this.animation)
        this.$emit('change')
      },
    },
  }
</script>

