<template>
  <div class="box-shadow-select">
    <div class="content" :class="{ 'is-show-detail': isShowDetail }">
      <el-select
        class="form-item-select"
        v-model="type"
        placeholder="默认"
        @change="handleTypeChange"
      >
        <el-option
          v-for="item in typeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div class="inner-content" v-if="isShowDetail">
      <n-form size="mini" class="inner-form" label-position='left' inline label-width='60px' @submit.native.prevent>
        <n-form-item label="边框位置" class="style-item">
          <el-select
            class="style-select"
            v-model="border.placement"
            multiple
            placeholder="请选择边框位置"
            @change="(v) => (border = { ...border, placement: v })"
          >
            <el-option
              v-for="item in placementList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="边框宽度" class="style-item">
          <el-select
            class="style-select"
            v-model="border.width"
            placeholder="请选择边框宽度"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (border = { ...border, width: v })"
          >
            <el-option
              v-for="item in translateList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="边框类型" class="style-item">
          <el-select
            class="style-select"
            v-model="border.type"
            placeholder="请选择边框类型"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (border = { ...border, type: v })"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="边框颜色" class="style-item">
          <el-color-picker
            class="color-picker"
            show-alpha
            v-model="border.color"
            @change="(v) => (border = { ...border, color: trimAll(v) })"
          ></el-color-picker>
        </n-form-item>
      </n-form>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { trimAll } from '@/utils'
  export default {
    name: 'BoxShadowSelect',
    props: {
      value: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        type: 'default',
        placementList: ['左', '右', '上', '下'],
        typeOptions: [
          {
            label: '大',
            value: 'l',
          },
          {
            label: '中',
            value: 'm',
          },
          {
            label: '小',
            value: 's',
          },
          {
            label: '无',
            value: 'none',
          },
          {
            label: '默认',
            value: 'default',
          },
          {
            label: '自定义',
            value: 'diy',
          },
        ],
        typeMap: {
          l: {
            placement: ['左', '右', '上', '下'],
            width: '4px',
            type: 'solid',
            color: '#000000',
          },
          m: {
            placement: ['左', '右', '上', '下'],
            width: '2px',
            type: 'solid',
            color: '#000000',
          },
          s: {
            placement: ['左', '右', '上', '下'],
            width: '1px',
            type: 'solid',
            color: '#000000',
          },
        },
        isShowDetail: false,
        translateList: ['1px', '2px', '3px', '4px', '5px', '6px'],
        typeList: [
          {
            label: '实线',
            value: 'solid',
          },
          {
            label: '虚线',
            value: 'dashed',
          },
          {
            label: '点线',
            value: 'dotted',
          },
        ],
      }
    },
    computed: {
      border: {
        get() {
          return this.value
        },
        set(n) {
          this.$emit('input', n)
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n) {
          let isMatch = false
          Object.keys(this.typeMap).forEach(type => {
            if (_.isEqual(n, this.typeMap[type])) {
              this.type = type
              this.isShowDetail = !['default', 'none'].includes(type)
              isMatch = true
            }
          })
          if (isMatch) {
            return
          }
          this.isShowDetail = Object.values(n).every((v) => (Array.isArray(v) && v.length) || (!Array.isArray(v) && v))
          this.type = this.isShowDetail ? 'diy' : this.type
        },
      },
    },
    methods: {
      trimAll,
      handleTypeChange(v) {
        if (v === 'diy') {
          this.border = {
            placement: this.border.placement.length ? this.border.placement : ['左', '右', '上', '下'],
            width: this.border.width ?? '1px',
            type: this.border.type ?? 'solid',
            color: 'rgba(0,0,0,0.6)',
          }
          this.isShowDetail = true
          return
        }
        if (v === 'default') {
          this.border = undefined
          this.isShowDetail = false
          return
        }
        if (v === 'none') {
          this.border = 'none'
          this.isShowDetail = false
          return
        }
        this.border = { ...this.typeMap[v] }
        this.isShowDetail = false
      },
    },
  }
</script>

