          transform: rotate(0deg);
          transition: all 0.3s ease;
        }
      }

      .form-item-select {
        flex: 1;
        margin-bottom: 2px;
      }
    }

    .inner-content {
      margin-left: -60px;
      .inner-form {
        .style-item {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 100%;
          margin-bottom: 8px;

          .el-form-item__content {
            flex: 1;
          }

          .style-select {
            width: 100%;
          }

          .color-picker {
            width: 100%;
