<template>
  <div class="font-select">
    <div
      class="content"
      :class="{ 'is-show-detail': isShowDetail }"
    >
      <el-select
        class="form-item-select"
        v-model="type"
        placeholder="默认"
        @change="handleFontTypeChange"
      >
        <el-option
          v-for="item in typeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
          <div :style='{...fontTypeMap[item.value], lineHeight: "34px"}'>{{item.label}}</div>
        </el-option>
      </el-select>
    </div>
    <div class="inner-content" v-if="isShowDetail">
      <n-form size="mini" inline label-width='60px' label-position='left' class="inner-form" @submit.native.prevent>
<!--        <n-form-item label="字体" class="style-item">-->
<!--          <el-select-->
<!--            class="style-select"-->
<!--            v-model="fontData.fontFamily"-->
<!--            placeholder="请选择字体"-->
<!--            filterable-->
<!--            allow-create-->
<!--            default-first-option-->
<!--            :automatic-dropdown="true"-->
<!--            @change="(v) => (fontData = { ...fontData, fontFamily: v })"-->
<!--          >-->
