<!--              :label="item"-->
<!--              :value="item"-->
<!--            >-->
<!--            </el-option>-->
<!--          </el-select>-->
<!--        </n-form-item>-->
        <n-form-item label="字号" class="style-item">
          <el-select
            class="style-select"
            v-model="fontData.fontSize"
            placeholder="请选择字号"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (fontData = { ...fontData, fontSize: v })"
          >
            <el-option
              v-for="item in fontSizeList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="字重" class="style-item">
          <el-select
            class="style-select"
            v-model="fontData.fontWeight"
            placeholder="请选择字重"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (fontData = { ...fontData, fontWeight: v })"
          >
            <el-option
              v-for="item in fontWeightList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="行高" class="style-item">
          <el-select
            class="style-select"
            v-model="fontData.lineHeight"
            placeholder="请选择行高"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (fontData = { ...fontData, lineHeight: v })"
          >
            <el-option
              v-for="item in lineHeightList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="颜色" class="style-item">
          <el-color-picker
            class="color-picker"
            show-alpha
            v-model="fontData.color"
            @change="(v) => (fontData = { ...fontData, color: v })"
          ></el-color-picker>
        </n-form-item>
        <n-form-item label="对齐" class="style-item">
          <el-button-group class='display-group'>
            <el-button
              class='display-button'
              v-for="item in textAlignOptions"
              :key="item.value"
              :type="fontData.textAlign === item.value ? 'primary' : 'default'"
              @click="
                fontData = {
                  ...fontData,
                  textAlign: item.value,
                }
              "
              >{{ item.label }}</el-button
            >
          </el-button-group>
        </n-form-item>
        <n-form-item label="换行" class="style-item">
          <el-button-group class='display-group'>
            <el-button
              class='display-button'
              v-for="item in whiteSpaceOptions"
              :key="item.value"
              :type="fontData.whiteSpace === item.value ? 'primary' : 'default'"
              @click="
                fontData = {
                  ...fontData,
                  whiteSpace: item.value,
                }
              "
            >{{ item.label }}</el-button
            >
          </el-button-group>
        </n-form-item>
      </n-form>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash'
  export default {
    name: 'FontSelect',
    props: {
      value: {
        type: Object,
      },
    },
    data() {
      return {
        type: 'default',
        typeOptions: [
          {
            label: '主标题',
            value: 'extraLarge',
          },
          {
            label: '标题',
            value: 'large',
          },
          {
            label: '小标题',
            value: 'medium',
          },
          {
            label: '正文',
            value: 'base',
          },
          {
            label: '正文（小）',
            value: 'small',
          },
          {
            label: '辅助文字',
            value: 'extraSmall',
          },
          {
            label: '默认',
            value: 'default',
          },
          {
            label: '自定义',
            value: 'diy',
          },
        ],
        fontTypeMap: {
          extraLarge: {
            fontFamily: undefined,
            fontSize: '20px',
            lineHeight: '1.5em',
            fontWeight: '400',
            textAlign: 'left',
            color: '#606266',
            whiteSpace: 'normal'
          },
          large: {
            fontFamily: undefined,
            fontSize: '18px',
            lineHeight: '1.5em',
            fontWeight: '400',
            textAlign: 'left',
            color: '#606266',
            whiteSpace: 'normal'
          },
          medium: {
            fontFamily: undefined,
            fontSize: '16px',
            lineHeight: '1.5em',
            fontWeight: '400',
            textAlign: 'left',
            color: '#606266',
            whiteSpace: 'normal'
          },
          base: {
            fontFamily: undefined,
            fontSize: '14px',
            lineHeight: '1.5em',
            fontWeight: '400',
            textAlign: 'left',
            color: '#606266',
            whiteSpace: 'normal'
          },
          small: {
            fontFamily: undefined,
            fontSize: '13px',
            lineHeight: '1.5em',
            fontWeight: '400',
            textAlign: 'left',
            color: '#606266',
            whiteSpace: 'normal'
          },
          extraSmall: {
            fontFamily: undefined,
            fontSize: '12px',
            lineHeight: '1.5em',
            fontWeight: '400',
            textAlign: 'left',
            color: '#606266',
            whiteSpace: 'normal'
          },
          default: {
            fontFamily: undefined,
            fontSize: undefined,
            lineHeight: undefined,
            fontWeight: undefined,
            textAlign: undefined,
            color: undefined,
            whiteSpace: undefined
          },
        },
        isShowDetail: false,
        fontFamilyList: [],
        fontSizeList: ['12px', '13px', '14px', '16px', '20px', '24px'],
        lineHeightList: ['1em', '1.5em', '12px', '13px', '14px', '16px', '20px', '24px'],
        fontWeightList: ['100', '400', '500', '600', '700', '800', '900'],
        textAlignOptions: [
          {
            label: '居左',
            value: 'left',
          },
          {
            label: '居中',
            value: 'center',
          },
          {
            label: '居右',
            value: 'right',
          },
          {
            label: '两端',
            value: 'justify',
          },
        ],
        whiteSpaceOptions: [
          {
            label: '超出换行',
            value: 'normal',
          },
          {
            label: '换行符换行',
            value: 'pre-wrap',
          },
          {
            label: '不换行',
            value: 'nowrap',
          },
        ]
      }
    },
    computed: {
      fontData: {
        get() {
          return this.value
        },
        set(n) {
          this.$emit('input', n)
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n) {
          let isMatch = false
          Object.keys(this.fontTypeMap).forEach(type => {
            if (_.isEqual(n, this.fontTypeMap[type])) {
              this.type = type
              this.isShowDetail = type !== 'default'
              isMatch = true
            }
          })
          if (isMatch) {
            return
          }
          this.type = this.isShowDetail ? 'diy' : this.type
          this.isShowDetail = Object.values(n).some(v => v)
        }
      }
    },
    methods: {
      handleFontTypeChange(v) {
        if (v === 'diy') {
          this.fontData = {
            fontFamily: this.fontData.fontFamily ?? undefined,
            fontSize: this.fontData.fontSize ?? '12px',
            lineHeight: this.fontData.lineHeight ?? '1.5em',
            fontWeight: this.fontData.fontWeight ?? '400',
            textAlign: this.fontData.textAlign ?? 'left',
            color: '#000000',
            whiteSpace: this.fontData.whiteSpace ?? 'normal',
          }
          this.isShowDetail = true
          return
        }
        this.fontData = { ...this.fontTypeMap[v] }
        this.isShowDetail = v !== 'default'
      },
    },
  }
</script>

