<template>
  <div class="box-shadow-select">
    <div
      class="content"
      :class="{ 'is-show-detail': isShowDetail }"
    >
      <el-select
        class="form-item-select"
        v-model="type"
        placeholder="默认"
        @change="handleTypeChange"
      >
        <el-option
          v-for="item in typeOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div class="inner-content" v-if="isShowDetail">
      <n-form size="mini" class="inner-form" label-position='left' inline label-width='60px' @submit.native.prevent>
        <n-form-item label="水平偏移" class="style-item">
          <el-select
            class="style-select"
            v-model="boxShadowData.x"
            placeholder="请选择水平偏移"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (boxShadowData = { ...boxShadowData, x: v })"
          >
            <el-option
              v-for="item in translateList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="垂直偏移" class="style-item">
          <el-select
            class="style-select"
            v-model="boxShadowData.y"
            placeholder="请选择垂直偏移"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (boxShadowData = { ...boxShadowData, y: v })"
          >
            <el-option
              v-for="item in translateList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="模糊半径" class="style-item">
          <el-select
            class="style-select"
            v-model="boxShadowData.blur"
            placeholder="请选择模糊半径"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="(v) => (boxShadowData = { ...boxShadowData, blur: v })"
          >
            <el-option
              v-for="item in translateList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="阴影尺寸" class="style-item">
          <el-select
            class="style-select"
            v-model="boxShadowData.spread"
            placeholder="请选择阴影尺寸"
            filterable
            allow-create
            default-first-option
            :automatic-dropdown="true"
            @change="
              (v) =>
                (boxShadowData = {
                  ...boxShadowData,
                  spread: v,
                })
            "
          >
            <el-option
              v-for="item in translateList"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="阴影颜色" class="style-item">
          <el-color-picker
            class="color-picker"
            show-alpha
            v-model="boxShadowData.color"
            @change="(v) => (boxShadowData = { ...boxShadowData, color: trimAll(v) })"
          ></el-color-picker>
        </n-form-item>
      </n-form>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { trimAll } from '@/utils'
  export default {
    name: 'BoxShadowSelect',
    props: {
      value: {
        type: Object,
        default: () => ({}),
      },
    },
    data() {
      return {
        type: 'default',
        typeOptions: [
          {
            label: '大',
            value: 'l',
          },
          {
            label: '中',
            value: 'm',
          },
          {
            label: '小',
            value: 's',
          },
          {
            label: '无',
            value: 'none',
          },
          {
            label: '默认',
            value: 'default',
          },
          {
            label: '自定义',
            value: 'diy',
          },
        ],
        typeMap: {
          l: {
            x: '4px',
            y: '4px',
            blur: '15px',
            spread: '0px',
            color: '#1F3858',
          },
          m: {
            x: '2px',
            y: '2px',
            blur: '10px',
            spread: '0px',
            color: '#1F3858',
          },
          s: {
            x: '1px',
            y: '1px',
            blur: '4px',
            spread: '0px',
            color: '#1F3858',
          },
        },
        isShowDetail: false,
        translateList: ['1px', '2px', '3px', '4px', '5px', '6px'],
      }
    },
    computed: {
      boxShadowData: {
        get() {
          return this.value
        },
        set(n) {
          this.$emit('input', n)
        },
      },
    },
    watch: {
      value: {
        deep: true,
        immediate: true,
        handler(n) {
          let isMatch = false
          Object.keys(this.typeMap).forEach(type => {
            if (_.isEqual(n, this.typeMap[type])) {
              this.type = type
              this.isShowDetail = !['default', 'none'].includes(type)
              isMatch = true
            }
          })
          if (isMatch) {
            return
          }
          this.isShowDetail = Object.values(n).every(v => v);
          this.type = this.isShowDetail ? 'diy' : this.type
        }
      }
    },
    methods: {
      trimAll,
      handleTypeChange(v) {
        if (v === 'diy') {
          this.boxShadowData = {
            x: this.boxShadowData.x ?? '1px',
            y: this.boxShadowData.y ?? '1px',
            blur: this.boxShadowData.blur ?? '4px',
            spread: this.boxShadowData.spread ?? '0px',
            color: '#1F3859',
          }
          this.isShowDetail = true
          return
        }
        if (v === 'default') {
          this.boxShadowData = undefined
          return
        }
        if (v === 'none') {
          this.boxShadowData = 'none'
          return
        }
        this.boxShadowData = { ...this.typeMap[v] }
      },
    },
  }
</script>

