<template>
  <div class="display-select">
    <div class="item-display" :class="{ 'is-show-detail': isShowDetail }">
      <el-button-group class="display-group">
        <el-button
          class='display-button'
          v-for="item in displayOptions"
          :key="item.value"
          :type="getType(item.value)"
          :disabled='disabled'
          @click="displayData = { ...displayData, display: item.value }"
        >
          {{ item.label }}
        </el-button>
      </el-button-group>
    </div>
    <div
      class="item-flex"
      v-if="displayData.display === 'flex' && isShowDetail"
    >
      <div class="item-display">
        <div class="label">方向</div>
        <el-button-group class="display-group">
          <el-button
            class='display-button'
            v-for="item in directionOptions"
            :key="item.value"
            :type="getDirectionType(item.value)"
            @click="
              displayData = {
                ...displayData,
                flexDirection: item.value,
              }
            "
          >
            {{ item.label }}
          </el-button>
        </el-button-group>
      </div>
      <div class="item-display">
        <div class="label">换行</div>
        <el-button-group class="display-group">
          <el-button
            class='display-button'
            v-for="item in flexWrapOptions"
            :key="item.value"
            :type="getFlexWrapType(item.value)"
            @click="
              displayData = {
                ...displayData,
                flexWrap: item.value,
              }
            "
          >
            {{ item.label }}
          </el-button>
        </el-button-group>
      </div>
      <div class="item-display">
        <div class="label">{{displayData.flexDirection === 'column' ? '水平对齐' : '垂直对齐'}}</div>
        <el-button-group class="display-group">
          <el-button
            class='display-button'
            v-for="item in alignItemsOptions"
            :key="item.value"
            :type="getItemsType(item.value)"
            @click="
              displayData = {
                ...displayData,
                alignItems: item.value,
              }
            "
          >
            {{ item.label }}
          </el-button>
        </el-button-group>
      </div>
      <div class="item-display">
        <div class="label">{{displayData.flexDirection === 'row' ? '水平对齐' : '垂直对齐'}}</div>
        <el-button-group class="display-group">
          <el-button
            class='display-button'
            v-for="item in justifyOptions"
            :key="item.value"
            :type="getJustifyType(item.value)"
            @click="
              displayData = {
                ...displayData,
                justifyContent: item.value,
              }
            "
          >
            {{ item.label }}
          </el-button>
        </el-button-group>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'DisplaySelect',
    props: {
      value: {
        type: Object,
        default: () => ({}),
      },
      disabled: {
        type: Boolean,
        default: false
      },
    },
    data() {
      return {
        displayOptions: [
          {
            label: '默认',
            value: undefined,
          },
          {
            label: '块级',
            value: 'block',
          },
          {
            label: '行内块级',
            value: 'inline-block',
          },
          {
            label: '弹性',
            value: 'flex',
          },
        ],
        directionOptions: [
          {
            label: '横向',
            value: 'row',
          },
          {
            label: '纵向',
            value: 'column',
          },
        ],
        flexWrapOptions: [
          {
            label: '允许换行',
            value: 'wrap',
          },
          {
            label: '不允许换行',
            value: 'nowrap',
          },
        ],
        alignItemsOptions: [
          {
            label: '左',
            value: 'flex-start',
          },
          {
            label: '中',
            value: 'center',
          },
          {
            label: '右',
            value: 'flex-end',
          },
          {
            label: '拉伸',
            value: 'stretch',
          },
        ],
        justifyOptions: [
          {
            label: '左',
            value: 'flex-start',
          },
          {
            label: '中',
            value: 'center',
          },
          {
            label: '右',
            value: 'flex-end',
          },
          {
            label: '两侧',
            value: 'space-between',
          },
          {
            label: '平均',
            value: 'space-around',
          },
        ],
        isShowDetail: true,
      }
    },
    computed: {
      displayData: {
        get() {
          return this.value
        },
        set(n) {
          this.$emit('input', n)
        },
      },
    },
    methods: {
      getType(type) {
        return this.displayData.display === type ? 'primary' : 'default'
      },
      getDirectionType(type) {
        return this.displayData.flexDirection === type ? 'primary' : 'default'
      },
      getFlexWrapType(type) {
        return this.displayData.flexWrap === type ? 'primary' : 'default'
      },
      getItemsType(type) {
        return this.displayData.alignItems === type ? 'primary' : 'default'
      },
      getJustifyType(type) {
        return this.displayData.justifyContent === type ? 'primary' : 'default'
      },
    },
  }
</script>

<style lang="less" scoped>
  .display-select::v-deep {
    .item-display {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 8px;
      font-size: 12px;
      &.is-show-detail {
        .arrow-icon {
          transform: rotate(90deg) !important;
        }
      }

      .label {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 60px;
        font-weight: 700;
      }

      .arrow-icon {
        display: block;
        margin-right: 10px;
        font-size: 12px;
        transform: rotate(0deg);
        transition: all 0.3s ease;
      }

      .display-group {
        flex: 1;
        display: flex;
        .display-button {
          width: 100%;
        }

        .el-button {
          padding: 9px 8px;
        }
      }
    }

    .item-flex {
      margin-bottom: 8px;
      margin-left: -60px;
      .label {
        font-weight: 400;
      }
    }
  }
</style>
