<template>
  <div class="margin-select">
    <div
      class="content"
      :class="{ 'is-show-detail': isShowDetail }"
    >
      <el-select
        class="form-item-select"
        v-model="margin"
        placeholder="可输入按回车选中"
        allow-create
        filterable
        default-first-option
      >
        <el-option
          v-for="item in marginOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>
    <div class="inner-content" v-if="isShowDetail">
      <div
        class="content"
        v-for="(detail, key) of marginDetail"
        :key="detail.label"
      >
        <div class="label">{{ detail.label }}</div>
        <el-select
          class="form-item-select"
          v-model="detail.value"
          placeholder="可输入按回车选中"
          allow-create
          filterable
          default-first-option
          @change="handleChange($event, key)"
        >
          <el-option
            v-for="item in marginOptionsInner"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'MarginSelect',
    props: {
      value: {
        type: Object,
        default: () => ({
          top: undefined,
          right: undefined,
          bottom: undefined,
          left: undefined,
        }),
      },
    },
    data() {
      return {
        isShowDetail: false,
        details: {
          top: {
            label: '上间距',
            value: undefined,
          },
          right: {
            label: '右间距',
            value: undefined,
          },
          bottom: {
            label: '下间距',
            value: undefined,
          },
          left: {
            label: '左间距',
            value: undefined,
          },
        },
        marginOptions: [
          {
            label: '超大（24px）',
            value: '24px',
          },
          {
            label: '大（20px）',
            value: '20px',
          },
          {
            label: '中（16px）',
            value: '16px',
          },
          {
            label: '小（12px）',
            value: '12px',
          },
          {
            label: '超小（8px）',
            value: '8px',
          },
          {
            label: '无（0px）',
            value: '0px',
          },
          {
            label: '默认',
            value: undefined,
          },
          {
            label: '自定义',
            value: '',
          },
        ],
        marginOptionsInner: [
          {
            label: '超大（24px）',
            value: '24px',
          },
          {
            label: '大（20px）',
            value: '20px',
          },
          {
            label: '中（16px）',
            value: '16px',
          },
          {
            label: '小（12px）',
            value: '12px',
          },
          {
            label: '超小（8px）',
            value: '8px',
          },
          {
            label: '无（0px）',
            value: '0px',
          },
          {
            label: '默认',
            value: undefined,
          },
        ],
      }
    },
    computed: {
      margin: {
        get() {
          const set = new Set(Object.values(this.marginDetail).map((o) => o.value))
          if (set.size === 1) {
            const value = this.marginDetail.top.value
            if (value !== '') {
              this.isShowDetail = false
            }
            return value
          } else {
            this.isShowDetail = true
            return ''
          }
        },
        set(n) {
          switch (n) {
            case '':
              this.isShowDetail = true
              break
            default:
              Object.keys(this.marginDetail).forEach(
                (key) => (this.marginDetail[key].value = n)
              )
              this.$emit('input', {
                top: n,
                right: n,
                bottom: n,
                left: n,
              })
              break
          }
        },
      },
      marginDetail: {
        get() {
          Object.keys(this.details).forEach(
            (key) => (this.details[key].value = this.value[key])
          )
          return this.details
        },
        set(n) {
          if (new Set(Object.values(n).map((o) => o.value)).size === 1) {
            this.margin = n.top.value
          } else {
            this.margin = ''
          }
          this.$emit('input', {
            top: n.top.value,
            right: n.right.value,
            bottom: n.bottom.value,
            left: n.left.value,
          })
        },
      },
    },
    methods: {
      handleChange(n, key) {
        const detail = this.marginDetail
        detail[key].value = n
        this.marginDetail = detail
      },
    },
  }
</script>

<style lang="less" scoped>
  .margin-select::v-deep {
    margin-bottom: 8px;

    .content {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &.is-show-detail {
        .arrow-icon {
          transform: rotate(90deg) !important;
        }
      }

      .label {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: 12px;
        font-weight: 700;
        width: 60px;
      }

      .form-item-select {
        flex: 1;
        margin-bottom: 2px;
      }
    }

    .inner-content {
      margin-left: -60px;
      .label {
        font-weight: 400;
      }
    }
  }
</style>
