<template>
  <el-dialog
    custom-class="style-panel-edit-dialog"
    title=""
    :visible="dialogVisible"
    :close-on-click-modal="true"
    :modal-append-to-body="true"
    :append-to-body="true"
    :before-close="beforeClose"
    width="311px"
    top="0"
  >
    <div class="dialog-content">
      <div class="dialog-header">
        <div class="title">{{title}}</div>
      </div>
      <div class="dialog-body">
        <n-form
          label-position="left"
          ref="editForm"
          :rules="rules"
          :model="editForm"
          @submit.native.prevent
          label-width="100px"
        >
          <n-form-item label="部位名称" prop="name">
            <el-input v-model="editForm.name"></el-input>
          </n-form-item>
          <n-form-item prop="query">
            <template slot='label'>
              <span>CSS选择器</span>
              <el-tooltip popper-class="tooltip-question" placement="top">
                <div slot="content">可输入CSS选择器，样式会应用到自身或内部指定的元素上，例如，>.label <br>注意：选择的部位为后代元素时，需要在前面添加空格，例如，空格.label 会匹配到根元素内部所有class为label的元素<br>如果不加空格，例如，:hover 会作用到根元素本身</div>
                <i style='margin-left: 5px;' class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <el-input type="textarea" :rows="3" v-model="editForm.query"></el-input>
          </n-form-item>
        </n-form>
      </div>
      <div class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type='primary' @click="handleEditConfirm">确 认</el-button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import { getValidators, required } from '@/utils/validateUtils'
export default {
  name: 'StylePanelEditDialog',
  props: {
    styleConfigList: {
      type: Array,
    }
  },
  data() {
    return {
      dialogVisible: false,
      title: '',
      editForm: {
        name: '',
        query: ''
      },
      beforeConfig: {
        name: '',
        query: ''
      },
      rules: {
        name: getValidators([
          {
            message: '请输入部位名称',
            include: required,
            required: true,
            trigger: 'change',
          },
        ]),
        query: getValidators([
          {
            message: '请输入CSS选择器',
            include: required,
            required: true,
            trigger: 'change',
          },
        ]),
      },
    }
  },
  methods: {
    open({name, query} = {}) {
      this.beforeConfig = {name, query}
      this.title = name ? '编辑部位' : '新增部位'
      this.editForm.name = name
      this.editForm.query = query
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.editForm.clearValidate()
      })
    },
    beforeClose(done) {
      this.dialogVisible = false
      done()
    },
    async handleEditConfirm() {
      await this.$refs.editForm.validate()
      if (this.styleConfigList.some(item => item.name === this.editForm.name && item.name !== this.beforeConfig.name)) {
        this.$message.error('部位名称不能重复！')
        return
      }
      if (this.styleConfigList.some(item => item.query === this.editForm.query && item.query !== this.beforeConfig.query)) {
        this.$message.error('CSS选择器不能重复！')
        return
      }
      if (this.title === '新增部位') {
        this.$emit('confirmAdd', this.editForm)
      } else {
        this.$emit('confirmEdit', this.editForm)
      }
      this.dialogVisible = false
    }
  }
}
</script>

