<template>
  <div class="props-panel">
    <a-tabs
      v-model="activeTabName"
      :tabBarGutter='10'
      tab-position='right'
      size="small"
      type="editable-card"
      :animated="false"
      @edit="onEdit"
      @tabClick="handleTabClick"
    >
      <a-tab-pane
        v-for="styleConfig in styleConfigList"
        :key="styleConfig.name"
        :tab="styleConfig.name"
        :closable="isColsable(styleConfig)"
      >
        <StylesForm
          v-if="activeTabName === styleConfig.name"
          :config="styleConfig"
          :animation.sync="styleConfig.animation"
          :styles.sync="styleConfig.styles"
          :isRoot="!styleConfig.query"
          @change="handleConfigChange"
          @edit="edit"
        />
      </a-tab-pane>
    </a-tabs>
    <EditDialog ref="editDialog" :styleConfigList="styleConfigList" @confirmAdd="handleAdd" @confirmEdit="handleEditConfirm" />
  </div>
</template>

<script>
  import _ from 'lodash'
  import { Tabs } from 'ant-design-vue'
  import StylesForm from './components/styles-form.vue'
  import EditDialog from './components/edit-dialog.vue'
  import { getPropsValues } from '@/renderer/tools/config'
  import { globalEmit } from '@/renderer/tools/eventBus'
  import { PROPS } from '@/config/eventKey'
  import styleToCss from 'style-object-to-css-string'
  import { addCssByStyle, getImageUrl } from '@/utils'
  import { styleConfig } from '@/config/code-var'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    name: 'StylePanel',
    components: {
      CssCode: () => import('@/components/CssCode'),
      StylesForm,
      EditDialog,
      ATabs: Tabs,
      ATabPane: Tabs.TabPane
    },
    inject: ['dndOptions', ...pageInject],
    data() {
      return {
        activeTabName: '根元素',
        styleConfigList: [],
        beforeConfigName: ''
      }
    },
    watch: {
      'dndOptions.selectedId': {
        immediate: true,
        handler() {
          this.initProps()
        }
      }
    },
    methods: {
      isColsable(config) {
        const currentElement = this.getElementPropsById(this.dndOptions.selectedId)
        const { styleConfigList: baseConfigList = [] } = getPropsValues({ elementType: currentElement.elementType, elementVersion: currentElement.elementVersion })
        return !baseConfigList.some(item => item.name === config.name && item.query === config.query)
      },
      onEdit(targetKey, action) {
        this[action](targetKey)
      },
      add() {
        this.$refs.editDialog.open()
      },
      edit(config) {
        this.beforeConfigName = config.name
        this.$refs.editDialog.open(config)
      },
      async remove(name) {
        await this.$confirm(`确认删除“${name}”部位吗？`)
        const filterList = this.styleConfigList.filter(item => item.name !== name)
        const currentElement = this.getElementPropsById(this.dndOptions.selectedId)
        const { styleConfigList: baseConfigList = [] } = getPropsValues({ elementType: currentElement.elementType, elementVersion: currentElement.elementVersion })
        const baseCopy = _.cloneDeep(baseConfigList)
        this.styleConfigList = _.cloneDeep(_.uniqBy(filterList.concat(baseCopy), 'name'))
        this.activeTabName = this.styleConfigList[0].name
        this.updateStyleConfigList()
        this.handleUpdateStyle()
      },
      handleTabClick(name) {
        // const currentStyle = this.styleConfigList.find(item => item.name === name)
        this.activeTabName = name
      },
      handleAdd({name, query}) {
        const newConfig = _.cloneDeep(styleConfig)
        newConfig.name = name
        newConfig.query = query
        this.styleConfigList = _.cloneDeep([...this.styleConfigList, newConfig])
        this.updateStyleConfigList()
        this.$nextTick(() => {
          this.handleTabClick(name)
        })
      },
      handleEditConfirm({name, query}) {
        const beforeConfig = this.styleConfigList.find(item => item.name === this.beforeConfigName)
        beforeConfig.name = name
        beforeConfig.query = query
        this.updateStyleConfigList()
        this.handleUpdateStyle()
        this.$nextTick(() => {
          this.handleTabClick(name)
        })
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      initProps() {
        this.activeTabName = '根元素'
        const currentElement = this.getElementPropsById(this.dndOptions.selectedId)
        const { styleConfigList: baseConfigList = [] } = getPropsValues({ elementType: currentElement.elementType, elementVersion: currentElement.elementVersion })
        const { styleConfigList } = currentElement
        const baseCopy = _.cloneDeep(baseConfigList)
        this.styleConfigList = _.cloneDeep(styleConfigList ? _.uniqBy(styleConfigList.concat(baseCopy), 'name') : baseCopy)
      },
      updateStyleConfigList() {
        globalEmit.call(this, PROPS, {
          elementId: this.dndOptions.selectedId,
          data: { styleConfigList: this.styleConfigList },
          callback: ({styleConfigList}) => {
            this.styleConfigList = _.cloneDeep(styleConfigList)
            this.handleUpdateStyle()
            const currentSelectConfig = this.styleConfigList.find(item => item.name === this.activeTabName) || this.styleConfigList[0]
            this.activeTabName = currentSelectConfig.name
          }
        })
      },
      handleUpdateStyle() {
        const pageId = this.getStore().layoutData.elementId
        const styleText = this.styleConfigList.map(item => {
          const { cssText, ...styleObject  } = JSON.parse(JSON.stringify(item.styles))
          if (!cssText && Object.keys(styleObject).length === 0) {
            return ''
          }
          if (styleObject.backgroundImage) {
            const isPic = new RegExp(/\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i).test(styleObject.backgroundImage || '')
            styleObject.backgroundImage = isPic ? `url(${getImageUrl(styleObject.backgroundImage)})` : styleObject.backgroundImage
          }
          const styleString = `${styleToCss(styleObject) || ''}${cssText || ''}`
          return `#${this.dndOptions.selectedId === pageId ? `page_${pageId}` : this.dndOptions.selectedId}${item.query}{${styleString}}`
        }).join('')
        const id = `static-style_${this.dndOptions.selectedId}`
        addCssByStyle(id, styleText)
      },
      handleConfigChange() {
        this.updateStyleConfigList()
        this.handleUpdateStyle()
      }
    },
  }
</script>

<style lang="less" scoped>
  .props-panel::v-deep {
    width: 100%;
    padding: 0;

    .ant-tabs-tab {
      width: 24px;
      height: auto !important;
      word-break: break-all;
      line-height: 1 !important;
      padding: 4px !important;
      writing-mode: vertical-rl;
      margin-right: 0 !important;
      &.ant-tabs-tab-active {
        margin-left: 0 !important;
      }
      .ant-tabs-close-x {
        margin: 2px 0 0 !important;
        width: 10px !important;
      }
    }

    .ant-tabs-right-content {
      padding-right: 4px !important;
    }
  }
</style>
