<template>
  <autocomplete
    v-bind="$attrs"
    :value="value"
    :fetch-suggestions="querySearch"
    :trigger-on-focus="false"
    @input="$emit('input', $event)"
    @change="handleChange($event)"
    @focus="$emit('focus', $event)"
    @blur="$emit('blur', $event)"
    @clear="$emit('clear')"
    @select="handleSelect"
  ></autocomplete>
</template>

<script>
import { Autocomplete } from 'element-ui'
import { debounce } from '@/utils'
export default {
  components: {
    Autocomplete,
  },
  props: {
    value: {
      type: String,
    },
    units: {
      type: Array,
      default: () => ['px', '%', 'vw', 'vh'],
    },
  },
  data() {
    return {}
  },
  created() {},
  mounted() {},
  methods: {
    querySearch(queryString, cb) {
      let result = []
      if (queryString && queryString.trim()) {
        const { units } = this
        result = units.reduce((arr, unit) => {
          // 全是数字时
          if (/^\d+$/.test(queryString)) {
            arr.push({ value: queryString + unit })
          }
          // 当结构是 [数字+非数字 ]时, 有两种情况
          else if (/^\d+\D+$/.test(queryString)) {
            // 除去前面数字的部分
            const num = queryString.match(/\d+/)[0]
            const end = queryString.replace(num, '')
            // 单位只填了部分,部分匹配
            if (unit.includes(end)) {
              arr.push({ value: num + unit })
            }
            //单位全匹配
            else if (end === unit) {
              arr.push({ value: queryString })
            }
          }

          return arr
        }, [])
      }
      cb(result)
    },
    handleSelect(e) {
      this.handleChange(e.value)
    },
    handleChange: debounce(function(val) {
      this.$emit('change', val)
    }, 100)
  },

  computed: {},

  watch: {},
}
</script>
